/**
 * Compose
 * @description 组合中间件
 * @param middleWares
 * @constructor
 */
function Compose(middleWares) {
    return function (ctx, next) {
        return new Promise(function (resolve) {
            var index = -1;
            // 中间件的返回值
            var middleWareQueueResults = Array.from({
                length: middleWares.length,
            }).fill(undefined);
            /**
             * loop
             * @description 迭代方法
             */
            var loop = function () {
                // 迭代完成了
                if (index + 1 >= middleWares.length) {
                    Promise.all(middleWareQueueResults.filter(function (t) { return t instanceof Promise; })).then(function () {
                        // 真正的迭代完成
                        if (next) {
                            var p = next();
                            if (p && p.then) {
                                p.then(function () {
                                    resolve();
                                });
                                return;
                            }
                        }
                        resolve();
                    });
                    return;
                }
                index++;
                // 调用中间件方法
                // @ts-ignore
                middleWareQueueResults[index] = middleWares[index](ctx, function () { return loop(); });
                return middleWareQueueResults[index];
            };
            loop();
        });
    };
}
export default Compose;
